<template>
	
	<view>
		
		<!-- 导航栏 -->
		<cu-custom bgColor="bg-gradual-blue">
			<block slot="content">注册</block>
		</cu-custom>
		
		<view class="wrap">
			<view class="top"></view>
			
			<block v-if="loginWay==1">
			<view class="content">
				<view class="title">欢迎登录</view>
				<input class="u-border-bottom" type="number" v-model="tel" placeholder="请输入手机号" /> 
				<view class="tips">未注册的手机号验证后自动创建账号</view>
				<!-- <button @tap="submit" :style="[inputStyle]" class="getCaptcha">获取短信验证码</button> -->
				<button  :style="[inputStyle]" class="getCaptcha" @click="onSMSSend">获取短信验证码</button>
				<view class="alternative">
					<view class="password" @tap="loginWay=3-loginWay">密码登录</view>
					<view class="issue" @click="goPassword">遇到问题</view>
				</view>
			</view>
			</block>
			
			<block v-else>
				<view class="content">
					<view class="title">欢迎登录</view>
					<input class="u-border-bottom" type="number" v-model="tel" placeholder="请输入手机号" /> 
					<input class="u-border-bottom" type="number" v-model="tel" placeholder="请输入密码" />
					<!-- <view class="tips">未注册的手机号验证后自动创建账号</view> -->
					<!-- <button @tap="submit" :style="[inputStyle]" class="getCaptcha">获取短信验证码</button> -->
					<button  :style="[inputStyle]" class="getCaptcha" @click="onSMSSend">登录</button>
					<view class="alternative">
						<view class="password" @tap="loginWay=1">手机登录</view>
						<view class="issue" @click="goPassword">遇到问题</view>
					</view>
				</view>
			</block>
			
			<view class="buttom">
				<view class="loginType">
					<view class="wechat item">
						<view class="icon"><u-icon size="70" name="weixin-fill" color="rgb(83,194,64)"></u-icon></view>
						微信
					</view>
					<view class="QQ item">
						<view class="icon"><u-icon size="70" name="qq-fill" color="rgb(17,183,233)"></u-icon></view>
						QQ
					</view>
				</view>
				<view class="hint">
					登录代表同意
					<text class="link">美团点评用户协议、隐私政策，</text>
					并授权使用您的美团点评账号信息（如昵称、头像、收获地址）以便您统一管理
				</view>
			</view>
			
			
		</view>
		
	</view>
	
</template>

<script>
export default {
	data() {
		return {
			tel: '',
			loginWay: 1
		}
	},
	computed: {
		inputStyle() {
			let style = {};
			let checkPhone = new RegExp(/^[1]([3-9])[0-9]{9}$/);
			if(this.tel || this.tel.length !== 0 || checkPhone.test(this.tel)) {
				style.color = "#fff";
				style.backgroundColor = this.$u.color['warning'];
			}
			return style;
		}
	},
	methods: {
		// 跳转到密码登录
		goPassword(){
			uni.navigateTo({
				url:"./login_v2"
			})
		},
		
		
		// 判断手机号是否输入正确
		onSMSSend() {
			let checkPhone = new RegExp(/^[1]([3-9])[0-9]{9}$/);
		
			if (!this.tel || this.tel.length == 0) {
				this.$tip.toast('请填写手机号');
				return;
			}
			if (!checkPhone.test(this.tel)) {
				this.$tip.toast('请输入正确的手机号');
				return false
			}
			// if (!this.smsCode || this.smsCode.length == 0) {
			// 	this.$tip.toast('请填短信验证码');
			// 	return;
			// }
			let loginParams = {
				mobile: this.tel,
				// captcha: this.smsCode
			};
			if(this.tel || this.tel.length !== 0 || checkPhone.test(this.tel)){
				this.submit();
			}
			// this.inputStyle()
			// this.PhoneLogin(loginParams).then((res) => {
			// 	console.log("res====》", res)
			// 	if (res.data.success) {
			// 		this.$tip.success('登录成功!')
			// 		this.$Router.replaceAll({
			// 			name: 'index'
			// 		})
			// 	} else {
			// 		this.$tip.error(res.data.message);
			// 	}
			// }).catch((err) => {
			// 	let msg = ((err.response || {}).data || {}).message || err.data.message || "请求出现错误，请稍后再试"
			// 	this.$tip.error(msg);
			// });
		},
		
		submit() {
			uni.navigateTo({
				url:"./code"
			})
			// if(this.$u.test.mobile(this.tel)) {
			// 	this.$u.route({
			// 		url: 'pages/template/login/code'
			// 	})
			// }
		}
	}
};
</script>

<style lang="scss" scoped>
.wrap {
	font-size: 28rpx;
	
	.content {
		width: 600rpx;
		margin: 80rpx auto 0;

		.title {
			text-align: left;
			font-size: 60rpx;
			font-weight: 500;
			margin-bottom: 100rpx;
		}
		input {
			text-align: left;
			margin-bottom: 10rpx;
			padding-bottom: 6rpx;
		}
		.tips {
			color: $u-type-info;
			margin-bottom: 60rpx;
			margin-top: 8rpx;
		}
		.getCaptcha {
			// background-color: #2b85e4;
			// background-color: #0093E9;
			background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);

			color: $u-tips-color;
			border: none;
			font-size: 30rpx;
			padding: 12rpx 0;
			
			&::after {
				border: none;
			}
		}
		.alternative {
			color: $u-tips-color;
			display: flex;
			justify-content: space-between;
			margin-top: 30rpx;
		}
	}
	.buttom {
		.loginType {
			display: flex;
			padding: 350rpx 150rpx 150rpx 150rpx;
			justify-content:space-between;
			
			.item {
				display: flex;
				flex-direction: column;
				align-items: center;
				color: $u-content-color;
				font-size: 28rpx;
			}
		}
		
		.hint {
			padding: 20rpx 40rpx;
			font-size: 20rpx;
			color: $u-tips-color;
			
			.link {
				color: $u-type-warning;
			}
		}
	}
}
</style>
